import { Field, InputType } from '@nestjs/graphql';
import { PartialType } from '@nestjs/mapped-types';
import { IsBoolean, IsEmail, IsNotEmpty, IsOptional, IsString, IsUUID } from 'class-validator';
import { User } from '../../../../../orm-config/entity/authentication/user.entity';
import { ColumnExists } from '../../../../../validation/custom/column-exists/column-exists.decorator';
import { CreateUserClientInput } from './create-user-client.input';

@InputType()
export class UpdateUserClientInput extends PartialType(CreateUserClientInput) {
  
  @Field(() => String)
  @IsUUID(4)
  id: string;

  @Field(() => String, { nullable: true })
  @IsOptional()
  @IsNotEmpty()
  @IsEmail()
  @ColumnExists(User)
  email: string;

  @Field(() => String, { nullable: true })
  @IsString()
  @IsOptional()
  @IsNotEmpty()
  firstName: string;
  
  @Field(() => String, { nullable: true })
  @IsString()
  @IsOptional()
  @IsNotEmpty()
  lastName: string;
  
  @Field(() => Boolean, { nullable: true })
  @IsBoolean()
  @IsOptional()
  isActive: boolean;
  
  @Field(() => String, { nullable: true })
  @IsString()
  @IsOptional()
  @IsNotEmpty()
  profilePicture: string;
  
}
